(function () {
  'use strict';
  angular.module('photogram').directive('actualSrc', function () {
    return {
      link: function postLink(scope, element, attrs) {
        attrs.$observe('actualSrc', function (newVal) {
          if (newVal !== undefined) {
            var img = new Image();
            img.src = attrs.actualSrc;
            angular.element(img).bind('load', function () {
              element.attr('src', attrs.actualSrc);
            });
          }
        });
      }
    }
  });

})();

(function () {
  'use strict';

  /**
   * @ngdoc directive
   * @name deleteItem
   *
   * @description
   * _Please update the description and restriction._
   *
   * @restrict A
   * */

  angular
    .module('photogram')
    .directive('deleteItem', deleteItemDirective);

  function deleteItemDirective($mdDialog, Toast) {
    return {
      restrict: 'A',
      link    : deleteItemLink,
      scope   : {
        model   : '=',
        item    : '=',
        onDelete: '='
      }
    };

    function deleteItemLink(scope, elem, attr) {

      elem.bind('click', function (event) {

        var confirm = $mdDialog.confirm()
                               .title('Confirm action')
                               .content('Are you sure you want to delete this item?')
                               .ok('Delete')
                               .cancel('Cancel')
                               .targetEvent(event)
          ;

        $mdDialog.show(confirm).then(function () {

          scope.Model.destroy(item).then(function (success) {
              Toast.simple('Item deleted.');
              scope.onDelete();
            },
            function (error) {
              Toast.simple(error.message);
            });

        });
      });


    }
  }

})();

(function () {
  'use strict';
  
  angular.module('photogram').directive('numbersOnly', function () {
    return {
      require: 'ngModel',
      link   : function (scope, element, attr, ngModelCtrl) {
        function fromUser(text) {
          if (text) {
            var transformedInput = text.replace(/[^0-9]/g, '');

            if (transformedInput !== text) {
              ngModelCtrl.$setViewValue(transformedInput);
              ngModelCtrl.$render();
            }
            return transformedInput;
          }
          return undefined;
        }

        ngModelCtrl.$parsers.push(fromUser);
      }
    };
  });

})();

(function () {
    'use strict';

    angular.module('photogram').directive('uploadImage', uploadImageDirective);

    function uploadImageDirective() {
        return {
            restrict: 'A',
            link    : uploadImageLink
        };

        function uploadImageLink(scope, elem, attr) {
            elem.bind('click', function () {
                var fileInput = angular.element('<input type="file" accept="image/x-png, image/gif, image/jpeg" max-size="2048" />');
                fileInput[0].click();
                fileInput.on('change', function (evt) {
                    var tempImage = evt.currentTarget.files[0];
                    var reader    = new FileReader();
                    reader.onload = function (evt) {
                        defer.resolve(evt.target.result);
                    };
                    reader.readAsDataURL(tempImage);
                });
            })
        }
    }

})();
