(function () {
    'use strict';
    angular.module('photogram').factory('ParseCloud', ParseCloudFactory);

    function ParseCloudFactory($q) {
        return {
            run: run
        };

        function run(action, data) {
            var defer = $q.defer();
            Parse.Cloud.run(action, data, {
                success: defer.resolve,
                error  : defer.reject
            });
            return defer.promise;
        }
    }

})();

(function () {
  'use strict';
  angular.module('photogram').factory('ParseFile', function ($q) {
    return {
      upload: upload
    };

    function upload(file, ext) {
      var defer = $q.defer();
      var filename = 'file.jpg';

      if (ext) {
        filename.replace('.jpg', ext);
      }
      
      new Parse.File(filename, file)
        .save({
          success: defer.resolve,
          error  : defer.reject
        });
      return defer.promise;
    }
  });

})();

