(function () {
    'use strict';
    angular.module('photogram').factory('User', UserFactory);

    function UserFactory($q, ParseCloud) {

        var User = Parse.User.extend({}, {
            signIn: function (data) {
                var defer = $q.defer();
                Parse.User.logIn(data.email, data.password, {
                    success: defer.resolve,
                    error  : defer.reject
                });

                return defer.promise;
            },

            signUp           : function (data) {
                var defer = $q.defer();
                var user  = new Parse.User();
                user.set({'name': data.name});
                user.set({'username': data.email});
                user.set({'email': data.email});
                user.set({'password': data.password});
                user.set({'roleName': 'User'});

                var acl = new Parse.ACL();
                acl.setPublicReadAccess(false);
                acl.setPublicWriteAccess(false);
                user.setACL(acl);
                user.save(null, {
                    success: defer.resolve,
                    error  : defer.reject
                });

                return defer.promise;
            },
            signInViaFacebook: function (authData) {

                var expiration = new Date();
                expiration.setSeconds(expiration.getSeconds() + authData.authResponse.expiresIn);
                expiration = expiration.toISOString();

                var facebookAuthData = {
                    'id'             : authData.authResponse.userID,
                    'access_token'   : authData.authResponse.accessToken,
                    'expiration_date': expiration
                }

                var defer = $q.defer();

                Parse.FacebookUtils.logIn(facebookAuthData, {
                    success: defer.resolve,
                    error  : defer.reject
                });

                return defer.promise;
            },

            findByEmail           : function (email) {

                var defer = $q.defer();

                Parse.Cloud.run('findUserByEmail', {email: email}, {
                    success: defer.resolve,
                    error  : defer.reject
                });

                return defer.promise;
            },
            updateWithFacebookData: function (data) {

                var defer = $q.defer();

                Parse.Cloud.run('saveFacebookPicture', {}, {
                    success: function () {
                        var user = Parse.User.current();
                        user.set({'email': data.email});
                        user.set({'username': data.email});
                        user.set({'name': data.name});
                        user.setACL(new Parse.ACL(user));
                        user.save(null, {
                            success: function () {
                                user.fetch().then(function (userFetched) {
                                    defer.resolve(userFetched);
                                }, function (error) {
                                    defer.reject(error);
                                })
                            },
                            error  : function (user, error) {
                                defer.reject(error);
                            }
                        });
                    },
                    error  : defer.reject
                });
                return defer.promise;
            },

            findUserByUsername: function (params) {
                return ParseCloud.run('findUserByUsername', params);
            },
            getPublicData     : function () {
                var defer = $q.defer();
                new Parse.Query('UserData')
                    .equalTo('user', Parse.User.current())
                    .first()
                    .then(function (userData) {
                        if (userData) {
                            defer.resolve(userData);
                        } else {
                            defer.reject(Parse.Promise.error({
                                code   : 1,
                                message: 'User Data not found'
                            }));
                        }
                    }, defer.reject);

                return defer.promise;
            },
            recoverPassword   : function (email) {
                var defer = $q.defer();
                Parse.User.requestPasswordReset(email, {
                    success: defer.resolve,
                    error  : defer.reject
                });
                return defer.promise;
            },
            destroy           : function () {
                var defer = $q.defer();
                Parse.User.current().destroy().then(defer.resolve, defer.reject);
                return defer.promise;
            },
            setPhoto          : function (parseFile) {
                var defer = $q.defer();
                var user  = Parse.User.current();
                user.set({'photo': parseFile});
                user.save(null, {
                    success: defer.resolve,
                    error  : defer.reject
                });
                return defer.promise;
            },
            follow            : function (otherUser) {
                // create an entry in the Follow table
                var follow = new Parse.Object('UserFolow');
                follow.set('from', Parse.User.current());
                follow.set('to', otherUser);
                follow.set('date', Date());
                follow.save();
            },
            all               : function (params) {
                return ParseCloud.run('getUsers', params);
            },
            create            : function (data) {
                return ParseCloud.run('createUser', data);
            },
            validateUsername  : function (input) {
                var defer = $q.defer();
                defer.resolve(true);

                //ParseCloud.run('validateUsername', input).then(function (data) {
                //    console.log(data);
                //    if (data == true) {
                //        defer.resolve();
                //    } else {
                //        defer.reject();
                //    }
                //});
                return defer.promise;
            },
            validateEmail     : function (input) {
                var defer = $q.defer();
                defer.resolve(true);
                //ParseCloud
                //    .run('validateEmail', input)
                //    .then(function (data) {
                //        console.log(data);
                //        if (data == true) {
                //            defer.resolve();
                //        } else {
                //            defer.reject();
                //        }
                //    });
                return defer.promise;
            },
            findUsername      : function (username) {
                var defer = $q.defer();
                new Parse.Query(this)
                    .equalTo('username', username)
                    .first({
                        success: defer.resolve,
                        error  : defer.reject
                    })
                return defer.promise;
            },
            update            : function (user) {
                var data = {
                    'id'      : user.id,
                    'name'    : user.name,
                    'email'   : user.email,
                    'username': user.email,
                    'password': user.password,
                    'gender'  : user.gender,
                    'photo'   : user.photo,
                    'roleName': user.roleName,
                }
                return ParseCloud.run('updateUser', data);
            },
            delete            : function (data) {
                return ParseCloud.run('destroyUser', data);
            },
            fetch             : function () {
                var defer = $q.defer();
                if (Parse.User.current()) {
                    Parse.User.current().fetch().then(defer.resolve, defer.reject);
                } else {
                    defer.reject();
                }
                return defer.promise;
            }

        });

        Object.defineProperty(User.prototype, 'name', {
            get: function () {
                return this.get('name');
            },
            set: function (val) {
                this.set('name', val);
            }
        });

        Object.defineProperty(User.prototype, 'username', {
            get: function () {
                return this.get('username');
            },
            set: function (val) {
                this.set('username', val);
            }
        });

        Object.defineProperty(User.prototype, 'gender', {
            get: function () {
                return this.get('gender');
            },
            set: function (val) {
                this.set('gender', val);
            }
        });

        Object.defineProperty(User.prototype, 'email', {
            get: function () {
                return this.get('email');
            },
            set: function (val) {
                this.set('email', val);
            }
        });

        Object.defineProperty(User.prototype, 'photo', {
            get: function () {
                return this.get('photo');
            },
            set: function (val) {
                this.set('photo', val);
            }
        });

        Object.defineProperty(User.prototype, 'photoThumb', {
            get: function () {
                return this.get('photoThumb');
            },
            set: function (val) {
                this.set('photoThumb', val);
            }
        });

        Object.defineProperty(User.prototype, 'roleName', {
            get: function () {
                return this.get('roleName');
            },
            set: function (val) {
                this.set('roleName', val);
            }
        });

        return User;

    }

})();

(function () {
  'use strict';
  angular.module('admin').controller('AdminActivityCtrl', AdminActivityController );

    function AdminActivityController($scope, $mdDialog, $mdToast, GalleryActivity) {

        // Pagination options
        $scope.rowOptions = [10, 20, 40];

        $scope.query = {
            filter: '',
            limit : 40,
            page  : 1,
            total : 0,
            order : '-createdAt',
            status: '',
            date  : null
        };

        $scope.items = [];

        $scope.openMenu = function ($mdOpenMenu, ev) {
            $mdOpenMenu(ev);
        };


        function showSimpleToast(message) {
            $mdToast.show(
                $mdToast.simple().content(message).action('OK').hideDelay(3000)
            );
        };

        function loadAll() {
            $scope.promise = GalleryActivity.all($scope.query).then(function (data) {
                $scope.items = data;
                data.map(function  (item) {
                    console.log(item);
                    console.log(item.attributes);
                })
            });
        };

        loadAll();

        function loadCount() {
            GalleryActivity.count($scope.query).then(function (total) {
                $scope.query.total = total;
            });
        }

        loadCount();


        $scope.onQueryChange = function () {
            $scope.query.page  = 1;
            $scope.query.total = 0;
            loadAll();
            loadCount();
        }

        $scope.onCreateGalleryActivity = function (ev) {

            $mdDialog.show({
                controller         : 'AdminGalleryActivityModalController',
                templateUrl        : 'app/admin/views/modal/admin.activity.modal.html',
                parent             : angular.element(document.body),
                targetEvent        : ev,
                locals             : {
                    item: null
                },
                clickOutsideToClose: true
            }).then(function (answer) {
                loadAll();
                loadCount();
            });
        };

        $scope.onPaginationChange = function (page, limit) {
            $scope.query.page  = page;
            $scope.query.limit = limit;
            loadAll();
        };


        $scope.isDate = function (date) {
            return angular.isDate(date);
        }

        $scope.updateItem = function (ev, place) {

            var objGalleryActivity = angular.copy(place);

            $mdDialog.show({
                controller         : 'AdminGalleryActivityModalController',
                templateUrl        : 'app/admin/views//modal/admin.activity.modal.html',
                parent             : angular.element(document.body),
                targetEvent        : ev,
                locals             : {
                    item: objGalleryActivity
                },
                clickOutsideToClose: true
            });
        };

        $scope.destroyItem = function (ev, place) {

            var confirm = $mdDialog.confirm()
                                   .title('Confirm action')
                                   .content('Are you sure you want to delete this place?')
                                   .ok('Delete')
                                   .cancel('Cancel')
                                   .targetEvent(ev);

            $mdDialog.show(confirm).then(function () {

                GalleryActivity.destroy(place).then(function (success) {
                        showSimpleToast('GalleryActivity deleted.');
                        loadAll();
                        loadCount();
                    },
                    function (error) {
                        showSimpleToast(error.message);
                    });

            });
        };

        $scope.aproveItem = function (place, isApproved) {

            place.isApproved = isApproved;
            place.unset('expiresAt');

            GalleryActivity.update(place).then(function (success) {
                showSimpleToast('GalleryActivity updated');
            }).catch(function (error) {
                showSimpleToast('There was an error');
            });

        };

    }
})();

(function () {
  'use strict';
  angular
    .module('admin')
    .controller('AdminCommentCtrl', function ($scope, $mdToast, $mdDialog, Comment, Auth) {

      // Pagination options.
      $scope.rowOptions = [10, 20, 40];

      $scope.query = {
        limit: 40,
        page: 1,
        total: 0,
        status: null
      };

      $scope.comments = [];

      var loadComments = function () {
        $scope.promise = Comment.all($scope.query).then(function (comments) {
          $scope.comments = comments;
        });
      }

      loadComments();

      var loadCount = function () {
        Comment.count($scope.query).then(function (total) {
          $scope.query.total = total;
        });
      }

      loadCount();

      var showToast = function (message) {
        $mdToast.show(
          $mdToast.simple()
                  .content(message)
                  .action('OK')
                  .hideDelay(3000)
        );
      };

      $scope.onQueryChange = function () {
        $scope.query.page = 1;
        loadComments();
        loadCount();
      }

      $scope.onPaginationChange = function (page, limit) {
        $scope.query.page  = page;
        $scope.query.limit = limit;
        loadComments();
      };

      $scope.onUpdateIsInappropriate = function (comment, value) {

        var objComment = angular.copy(comment);

        objComment.isInappropriate = value;

        Comment.update(objComment).then(function (success) {
          showToast('Comment updated');
        }, function (error) {
          showToast('There was an error');
        });
      };

      $scope.openMenu = function ($mdOpenMenu, ev) {
        $mdOpenMenu(ev);
      };
    })
})();

